
.tarea-contenedor{
    width: 100%;
    min-height: 65px;
    background-color: #1b1b32;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    border: 1px solid white;
    cursor: pointer;
}
.tarea-texto{
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
}
.tarea-icono{
    width: 35px;
    height: 35px;
    margin: 5px;
    cursor: pointer;
}
.tarea-contenedor.completada{
    background-color: #5a01a7;
    text-decoration: line-through;
}

@media screen and (min-width: 1024px){

    .tarea-contenedor{
        width: 550px;
    }
}